import Link from "next/link";
import styles from "../styles/styles.module.scss";
import sword from "../assets/sword.webp"
import Image from "next/image";
const Index = () => {
  return (
    <body>
      <div className={styles.body}>
        <header>
          <Image src={sword} alt="logotypeSword" className={styles.image}/>
          <nav className={styles.nav}>
            <Link href="/" className={styles.Link}>Главная</Link>
            <Link href="/catalogs" className={styles.Link}>Каталог</Link>
          </nav>
        </header>
        <h1 className={styles.title}>Meraviglioso</h1>
        <div className={styles.container}>
          <h1>Hello World!</h1>
          <p className={styles.subtitle}>hi</p>
        </div>
      </div>
    </body>
  );
};

export default Index;
